import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LoginComponent } from "./login/login.component";
import { NewProduitComponent } from "./new-produit/new-produit.component";
import { NewCategorieComponent } from "./new-categorie/new-categorie.component";
import { EditProduitComponent } from "./edit-produit/edit-produit.component";
import { EditCategorieComponent } from "./edit-categorie/edit-categorie.component";
import { ProduitsComponent } from "./produits/produits.component";
import { CategoriesComponent } from "./categories/categories.component";
import { RegisterComponent } from "./register/register.component";

const appRoutes : Routes = [
    {path : "login", component : LoginComponent},
    {path : "new-produit", component : NewProduitComponent},
    {path : "new-categorie", component : NewCategorieComponent},
    {path : "edit-produit/:id", component : EditProduitComponent},
    {path : "edit-categorie/:id", component : EditCategorieComponent},
    {path : "produits", component : ProduitsComponent},
    {path : "categories", component : CategoriesComponent},
    {path : "register", component : RegisterComponent},
    {path : "", redirectTo : "/login", pathMatch:"full"}
]
@NgModule({
    imports : [RouterModule.forRoot(appRoutes)],
    exports : [RouterModule]
})
export class RoutingModule {

}


