import { Component, OnInit } from '@angular/core';
import { CategorieService } from '../../services/categorie.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Categorie } from '../../models/categorie.model';
import { error } from 'protractor';

@Component({
  selector: 'app-edit-categorie',
  templateUrl: './edit-categorie.component.html',
  styleUrls: ['./edit-categorie.component.css']
})
export class EditCategorieComponent implements OnInit {
  private id : number;
  private categorie : any;
  private categorieFinal : any;

  constructor(private categorieService:CategorieService,private router:Router,
    private activedRoute:ActivatedRoute) {
    this.id = activedRoute.snapshot.params['id'];
   }

  ngOnInit() {
    this.categorieService.getCategorie(this.id).subscribe(donnees => {
      try {
        this.categorie = donnees;
      } catch (error) {
        
      }
    },error => {
      this.router.navigateByUrl("/new-categorie");
    });
  }
  
  editerCategorie(){
    this.categorieService.editCategorie(this.id,this.categorie).subscribe(donnees=>{
      this.categorieFinal = donnees;
      this.initialiser();
    },error => {
      console.error("Erreur de connexion !")
    });
  }

  initialiser(){
    this.categorie = new Categorie();
  }
}



