import { Component, OnInit } from '@angular/core';
import { CategorieService } from '../../services/categorie.service';
import { error } from 'protractor';
import { Router } from '@angular/router';
import { isNumber } from 'util';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {
  private categories : any;
  private pages : Array<number>;
  private page : number = 0;
  private taille : number = 7;

  constructor(private categorieService:CategorieService,private router:Router) { }

  ngOnInit() {
    this.loadData();
  }

  loadData(){
    this.categorieService.getAllCategoriePage(this.page,this.taille).subscribe(donnees => {
      this.categories = donnees;
      this.pages = new Array(this.categories.totalPages);
    },error=>{
      console.error("Erreur de connexion !");
    });
  }

  modifier(id:number){
    this.router.navigateByUrl("/edit-categorie/"+id)
  }

  nextPage(idPage:number){
    this.page = idPage;
    this.loadData();
  }

  supprimer(id:number){
    if (confirm('Voulez-vous vraiment supprimer ')){
      this.categorieService.deleteCategorie(id).subscribe();
      this.loadData();
    }
  }

}



