import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { NewProduitComponent } from './new-produit/new-produit.component';
import { NewCategorieComponent } from './new-categorie/new-categorie.component';
import { ProduitsComponent } from './produits/produits.component';
import { CategoriesComponent } from './categories/categories.component';
import { EditProduitComponent } from './edit-produit/edit-produit.component';
import { EditCategorieComponent } from './edit-categorie/edit-categorie.component';
import { RegisterComponent } from './register/register.component';
import { HttpClientModule } from '@angular/common/http';
import { AuthentificationService } from '../services/authentifier.service';
import { RoutingModule } from './app.routing.module';
import { FormsModule } from '@angular/forms';
import { ProduitService } from '../services/produit.service';
import { CategorieService } from '../services/categorie.service';
import { RegisterService } from '../services/register.service';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    NewProduitComponent,
    NewCategorieComponent,
    ProduitsComponent,
    CategoriesComponent,
    EditProduitComponent,
    EditCategorieComponent,
    RegisterComponent
  ],
  imports: [
    BrowserModule,HttpClientModule,RoutingModule,FormsModule
  ],
  providers: [AuthentificationService,ProduitService,CategorieService,RegisterService],
  bootstrap: [AppComponent]
})
export class AppModule { }



