import { Component, OnInit } from '@angular/core';
import { ProduitService } from '../../services/produit.service';
import { Produit } from '../../models/produit.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-produits',
  templateUrl: './produits.component.html',
  styleUrls: ['./produits.component.css']
})
export class ProduitsComponent implements OnInit {
  private produits : any;
  private pages : Array<number>;
  private taille : number = 7;
  private page : number = 0;

  constructor(public produitService:ProduitService,private router:Router) { }

  ngOnInit() {
    this.loadData();
  }

  loadData(){
    this.produitService.getAllProduitPage(this.page,this.taille).subscribe(donnees => {
      this.produits = donnees;
      this.pages = new Array(this.produits.totalPages);
    },error=>{
      console.error(error);
    });
  }

  modifier(id:number){
    this.router.navigateByUrl("/edit-produit/"+id);
  }

  nextPage(idPage:number){
    this.page = idPage;
    this.loadData();
  }

  supprimer(id:number){
    if (confirm('Voulez-vous vraimant supprimer !')){
      this.produitService.deleteProduit(id).subscribe();
      this.loadData();
    }
  }

}



