import { Component, OnInit } from '@angular/core';
import { User } from '../../models/user.model';
import { AuthentificationService } from '../../services/authentifier.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private user:User = new User();

  constructor(private auth:AuthentificationService,private router:Router) { }

  ngOnInit() {
    this.auth.viderToken();
  }

  authentifier(){
    this.auth.login(this.user).subscribe(donnees => {
      this.auth.saveToken(donnees.headers.get("Authorization"));
      this.router.navigateByUrl("/new-produit");
    });
  }

}



