import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { AuthentificationService } from "./authentifier.service";
import { Categorie } from "../models/categorie.model";

@Injectable()
export class CategorieService {
    
    constructor(private http : HttpClient,private auth : AuthentificationService){}

    getAllCategorie(){
        return this.http.get("http://localhost:8080/allCategorie",
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})})
    }

    getAllCategoriePage(page:number,taille:number){
        return this.http.get("http://localhost:8080/allCategoriePage?page="+page+"&taille="+taille,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})})
    }

    getCategorie(id:number){
        return this.http.get("http://localhost:8080/findCategorie/"+id,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})})
    }

    deleteCategorie(id:number){
        return this.http.get("http://localhost:8080/deleteCategorie/"+id,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})})
    }

    saveCategorie(categorie : Categorie){
        return this.http.post("http://localhost:8080/addCategorie",categorie,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    editCategorie(id : number,categorie : Categorie){
        return this.http.post("http://localhost:8080/editCategorie/"+id,categorie,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    isAdmin(){
        return this.auth.isAdmin();
    }

    isConnect(){
        if (this.auth.getJwtToken() != null){
          return true;
        }
        return false;
    }

}


