import { Component, OnInit } from '@angular/core';
import { ProduitService } from '../../services/produit.service';
import { Produit } from '../../models/produit.model';

@Component({
  selector: 'app-new-produit',
  templateUrl: './new-produit.component.html',
  styleUrls: ['./new-produit.component.css']
})
export class NewProduitComponent implements OnInit {
  private produit : Produit = new Produit();
  private produitFinal : any;

  constructor(private produitService:ProduitService) { }

  ngOnInit() {
  }

  addProduit(){
    this.produitService.saveProduit(this.produit).subscribe(donnees => {
      this.produitFinal = donnees;
      this.initialiser();
    });
  }

  initialiser(){
    this.produit = new Produit();
  }

}



