import { Component, OnInit } from '@angular/core';
import { CategorieService } from '../../services/categorie.service';
import { Categorie } from '../../models/categorie.model';
import { error } from 'protractor';

@Component({
  selector: 'app-new-categorie',
  templateUrl: './new-categorie.component.html',
  styleUrls: ['./new-categorie.component.css']
})
export class NewCategorieComponent implements OnInit {
  private categorie:Categorie = new Categorie();
  private categorieFinal : any;

  constructor(private categorieService:CategorieService) { }

  ngOnInit() {
  }

  addCategorie(){
    this.categorieService.saveCategorie(this.categorie).subscribe(donnees => {
      this.categorieFinal = donnees;
      this.initialiser();
    },error => {
      console.error("Réessayer plus tard !");
    });
  }

  initialiser(){
    this.categorie = new Categorie();
  }

}



