import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { AuthentificationService } from "./authentifier.service";
import { Produit } from "../models/produit.model";

@Injectable()
export class ProduitService {
    
    constructor(private http:HttpClient,private auth:AuthentificationService){}

    getAllProduit(){
        return this.http.get("http://localhost:8080/allProduit",
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    getAllProduitPage(page:number,taille:number){
        return this.http.get("http://localhost:8080/allProduitPage?page="+page+"&taille="+taille,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }
    
    saveProduit(produit:Produit){
        return this.http.post("http://localhost:8080/addProduit",produit,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    getProduit(id:number){
        return this.http.get("http://localhost:8080/findProduit/"+id,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    deleteProduit(id:number){
        return this.http.get("http://localhost:8080/deleteProduit/"+id,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})});
    }

    editProduit(id:number,produit:Produit){
        return this.http.post("http://localhost:8080/editProduit/"+id,produit,
        {headers : new HttpHeaders({'Authorization' : this.auth.getPrefixToken() + this.auth.getJwtToken()})})
    }

    isAdmin(){
        return this.auth.isAdmin();
    }

    isConnect(){
        if (this.auth.getJwtToken()!= null){
            return true;
        }
        return false;
    }

}



