import { Component, OnInit } from '@angular/core';
import { ProduitService } from '../../services/produit.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Produit } from '../../models/produit.model';
import { error } from 'protractor';

@Component({
  selector: 'app-edit-produit',
  templateUrl: './edit-produit.component.html',
  styleUrls: ['./edit-produit.component.css']
})
export class EditProduitComponent implements OnInit {
  private id : number;
  private produit : any;
  private produitFinal : any;

  constructor(private produitService:ProduitService,private router:Router,private activedRoute:ActivatedRoute) {
    this.id = activedRoute.snapshot.params['id'];
    this.produitService.getProduit(this.id).subscribe(donnees => {
      try {
        this.produit = donnees;
      } catch (error) {
        
      }
      
    },error => {
      console.error("Erreur !");
      this.router.navigateByUrl("/new-produit");
    });
   }

  ngOnInit() {

  }

  editerProduit(){
    this.produitService.editProduit(this.id,this.produit).subscribe(donnees => {
      this.produitFinal = donnees;
      this.initialiser();
    },error=>{
      console.error("Réessayer plus tard !");
    })
  }

  initialiser(){
    this.produit = new Produit();
  }


}



